export default {
	data() {
		return {
			jokeList: [],
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			showTop:false
		}
	},
	methods: {
		getJoke() {
			this.$minApi.getJoke({
				key: "a5d995147d87d58091d1bf504b322f55"
			}).then(data => {
				console.log(data)
				this.jokeList = data.data.result
			})
		},
		lower(e) {
			console.log(e)
			this.showTop=true
		},
		scroll(e) {
			// console.log(e)
			this.old.scrollTop = e.detail.scrollTop
		},
		goTop(e) {
			this.showTop=false
			this.scrollTop = this.old.scrollTop
			this.$nextTick(() => {
				this.scrollTop = 0
			});
		}
	},
	created() {
		this.getJoke()
	}
}
